import { Component, OnInit } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';


@Component({
  selector: 'app-why-dialog',
  templateUrl: './why-dialog.component.html',
  styleUrls: ['./why-dialog.component.css']
})
export class WhyDialogComponent implements OnInit {


  isTab$: Observable<boolean> = this.breakpointObserver.observe([Breakpoints.Tablet , Breakpoints.Handset, Breakpoints.Small])
  .pipe(
    map(result => result.matches),
    shareReplay()
  );
  constructor(private breakpointObserver: BreakpointObserver) {}


  ngOnInit(): void {
  }

}
